var _this;

	export default {
		data() {
			return {
				CustomBar:0,
				inputValue:0,
				shopList:[]
			};
		},
		methods: {
			goBack(){
				uni.navigateBack({
					delta:1
				})
			},
			InputFocus(e) {
				this.inputValue = e.detail.height
			},
			InputBlur(e) {
				this.inputValue = 0
			},
			searchShop(e) {
				let key = e.detail.value.toLowerCase();
				let list = this.icon;
				for (let i = 0; i < list.length; i++) {
					let a = key;
					let b = list[i].name.toLowerCase();
					if (b.search(a) != -1) {
						list[i].isShow = true
					} else {
						list[i].isShow = false
					}
				}
				this.icon = list
			},
			systemInfo(){
				uni.getSystemInfo({
					success: (res) => {
						this.CustomBar=res.windowTop
					}
				})
			},
			
			recordShop(){
				uni.navigateTo({
					url:'../record-shop/record-shop'
				})
			},
			checkShopDetail(item){
				//查看门店人员列表
				uni.navigateTo({
					url:'../clerk-list/clerk-list?shopID='+item.id
				})
			},
			//获得门店列表
			getShopList(){
				this.$ajax('MyShops',{address:''},res=>{
					this.shopList=res;
				})
			}
		},
		onLoad(){
			this.systemInfo();
			this.getShopList();
		}
	}
